<template>
  <div class="header" @click="backHome">
    <p class="Hometown" @click.stop='myHomeTown'>离石</p>
    <img src="@/assets/img/family.jpg" alt="">
    <div></div>
  </div>
</template>

<script>
  export default {
    props:['flag'],
    methods:{
      backHome(){
        this.$router.push('/pages/home')
      },
      myHomeTown(){
        if(this.flag === "home"){
          alert("这是我的家乡")
        }else{
          history.back()
        }
      }
    }
  }
</script>

<style scoped>
.header{
  width:100%;
  height:4rem;
  border-bottom:.1rem solid #ccc;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color:#fff(116, 69, 245);
  padding:1rem;
  box-sizing:border-box;
  background: orange
}
.header>img{
  width:10%;
}
.Hometown{
  font-size:1.4rem;
  font-weight: bold;
  color:#000;
}
</style>


